import { Pipe, PipeTransform } from '@angular/core';
import { KPIDetails } from '../datapoint-selection.model';

@Pipe({
  name: 'filterDatapoints'
})
export class FilterDatapointsPipe implements PipeTransform {
  transform(datapoints: KPIDetails[], searchString: string): KPIDetails[] {
    if (!searchString || !datapoints || !Array.isArray(datapoints)) {
      return datapoints;
    }
    const lowerCaseSearchString = searchString.toLowerCase().trim();
    return datapoints.filter(datapoint =>
      this.includesSearchStringIgnoringCasing(datapoint, lowerCaseSearchString)
    );
  }

  private includesSearchStringIgnoringCasing(
    datapoint: KPIDetails,
    lowerCaseSearchString: string
  ): boolean {
    const label = datapoint.label?.toLowerCase();
    if (label && label.includes(lowerCaseSearchString)) {
      return true;
    }

    const fragment = datapoint.fragment?.toLowerCase();
    if (fragment && fragment.includes(lowerCaseSearchString)) {
      return true;
    }

    const series = datapoint.series?.toLowerCase();
    if (series && series.includes(lowerCaseSearchString)) {
      return true;
    }

    return false;
  }
}
