import { IIdentified, IManagedObject } from '@c8y/client';
import { MillerViewComponent } from '@c8y/ngx-components/assets-navigator';

export const DATAPOINT_LIBRARY_FRAGMENT = 'c8y_Kpi';

export interface KPIDetails {
  fragment: string;
  series: string;
  orientation?: string;
  __target?: IIdentified;
  __active?: boolean;
  __template?: string | number;
  unit?: string;
  min?: number;
  color?: string;
  max?: number;
  label?: string;
  target?: number;
  yellowRangeMax?: number;
  yellowRangeMin?: number;
  redRangeMin?: number;
  redRangeMax?: number;
  description?: string;
  renderType?: string;
  lineType?: string;
  yAxisType?: string;
  [key: string]: any;
}

export interface ManagedObjectKPI extends IManagedObject {
  [DATAPOINT_LIBRARY_FRAGMENT]: KPIDetails;
}

export interface DatapointAction {
  label: string;
  callback: (datapoint: KPIDetails) => void;
  icon: string;
}

export interface DatapointAttributesFormConfig {
  showTarget: boolean;
  showRange: boolean;
  showYellowRange: boolean;
  showRedRange: boolean;
  showChart: boolean;
  showFormIfTemplateWasSelected: boolean;
}

export type DatapointSelectorModalAssetSelectorOptions = Omit<
  MillerViewComponent,
  | 'multi'
  | 'singleColumn'
  | 'onSelected'
  | 'millerViewWrapper'
  | 'columns'
  | 'ngOnInit'
  | 'ngOnChanges'
  | 'addNewColumn'
  | 'onSelectionChange'
>;
