import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import {
  Component,
  forwardRef,
  Input,
  OnChanges,
  OnInit,
  Optional,
  Output,
  SimpleChanges
} from '@angular/core';
import {
  AbstractControl,
  ControlValueAccessor,
  FormArray,
  FormBuilder,
  NG_VALIDATORS,
  NG_VALUE_ACCESSOR,
  ValidationErrors,
  Validator,
  ValidatorFn
} from '@angular/forms';
import { IResultList } from '@c8y/client';
import { C8yValidators } from '@c8y/ngx-components';
import { WidgetConfigComponent } from '@c8y/ngx-components/context-dashboard';
import { from, Observable } from 'rxjs';
import { map, shareReplay, take, tap } from 'rxjs/operators';
import { DatapointLibraryService } from '../datapoint-library.service';
import {
  DatapointAction,
  DatapointAttributesFormConfig,
  KPIDetails,
  ManagedObjectKPI
} from '../datapoint-selection.model';
import { DatapointSelectorModalOptions } from '../datapoint-selector-modal/datapoint-selector-modal.model';
import { DatapointSelectorService } from '../datapoint-selector.service';

@Component({
  selector: 'c8y-datapoint-selection-list',
  templateUrl: './datapoint-selection-list.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: forwardRef(() => DatapointSelectionListComponent)
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => DatapointSelectionListComponent),
      multi: true
    }
  ]
})
export class DatapointSelectionListComponent
  implements ControlValueAccessor, Validator, OnInit, OnChanges
{
  @Input() actions: DatapointAction[] = [];
  @Input() allowDragAndDrop = true;
  @Input() config: Partial<DatapointSelectorModalOptions> = {};
  @Input() defaultFormOptions: Partial<DatapointAttributesFormConfig> = {};
  @Input() maxActiveCount: number;
  @Input() minActiveCount = 1;
  @Input() resolveContext = true;
  @Input() listTitle = '';
  formArray: FormArray;
  datapointLibraryEntries: Observable<IResultList<ManagedObjectKPI>>;
  maxActiveCountReached = false;

  @Output() isValid: Observable<boolean>;
  @Output() change: Observable<any[]>;
  private usedValidators: { [key: string]: ValidatorFn } = {};

  constructor(
    private datapointSelector: DatapointSelectorService,
    private datapointLibrary: DatapointLibraryService,
    private formBuilder: FormBuilder,
    @Optional() private widgetComponent: WidgetConfigComponent
  ) {
    this.formArray = this.formBuilder.array([]);
    this.isValid = this.formArray.statusChanges.pipe(map(status => status === 'VALID'));
    this.datapointLibraryEntries = from(this.datapointLibrary.getFirstDatapointLibraryPage()).pipe(
      shareReplay()
    );
    this.change = this.formArray.valueChanges.pipe(map(res => this.transformValue(res)));
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (!changes.maxActiveCount && !changes.minActiveCount) {
      return;
    }
    if (changes.maxActiveCount) {
      this.usedValidators.maxActiveCount = C8yValidators.maxActiveCount(this.maxActiveCount);
    }

    if (changes.minActiveCount) {
      this.usedValidators.minActiveCount = C8yValidators.minActiveCount(this.minActiveCount);
    }
    const validators = Object.values(this.usedValidators);
    this.formArray.setValidators(validators);
  }

  registerOnTouched(fn: any): void {
    this.formArray.valueChanges.pipe(take(1)).subscribe(fn);
  }

  validate(_control: AbstractControl): ValidationErrors {
    return this.formArray.valid ? null : { formInvalid: {} };
  }

  ngOnInit(): void {
    const context = this.widgetComponent?.context;
    if (context?.id && this.resolveContext) {
      const { name, id, c8y_IsDevice } = context;
      this.config.contextAsset = { name, id, c8y_IsDevice };
    }
  }

  writeValue(obj: KPIDetails[]): void {
    this.formArray.clear();
    if (obj?.length) {
      obj.forEach(val => {
        const formgroup = this.formBuilder.group({ details: [] });
        formgroup.patchValue({ details: val });
        this.formArray.push(formgroup);
      });
    }
    this.calculateMaxActiveCount();
  }

  registerOnChange(fn: any): void {
    this.formArray.valueChanges
      .pipe(
        map(res => this.transformValue(res)),
        // check maxActiveCount
        tap(() => {
          this.calculateMaxActiveCount();
        })
      )
      .subscribe(fn);
  }

  add() {
    const allowChangingContext =
      !this.widgetComponent?.isDeviceTypeDashboard && this.config?.allowChangingContext !== false;
    this.datapointSelector
      .selectDataPoints({
        ...(this.config || {}),
        selectedDatapoints: this.transformValue(this.formArray.value),
        defaultActiveState: true,
        allowChangingContext,
        allowSearch: !this.config?.contextAsset
      })
      .then(
        result => {
          this.writeValue(result);
        },
        () => {
          // nothing to do, modal was closed
        }
      );
  }

  onItemRemoved(index: number) {
    this.formArray.removeAt(index);
  }

  drop(event: CdkDragDrop<KPIDetails[]>) {
    const currentSorting = this.formArray.value;
    moveItemInArray(currentSorting, event.previousIndex, event.currentIndex);
    this.formArray.setValue(currentSorting);
  }

  private transformValue(formArrayValue: any[]) {
    if (!formArrayValue) {
      return [];
    }
    return formArrayValue.map(tmp => Object.assign({}, ...Object.values(tmp)));
  }

  private calculateMaxActiveCount() {
    if (this.maxActiveCount) {
      const currentlyActive = this.formArray.value.filter(tmp => tmp.details?.__active).length;
      this.maxActiveCountReached = currentlyActive >= this.maxActiveCount;
    }
    this.maxActiveCountReached = false;
  }
}
