import { Pipe, PipeTransform } from '@angular/core';
import { KPIDetails } from '../datapoint-selection.model';

@Pipe({
  name: 'includesDatapoint'
})
export class IncludesDatapointPipe implements PipeTransform {
  transform(datapointList: KPIDetails[], datapoint?: KPIDetails): boolean {
    if (!Array.isArray(datapointList) || !datapoint) {
      return false;
    }
    return datapointList.some(
      tmp =>
        tmp.fragment === datapoint.fragment &&
        tmp.series === datapoint.series &&
        tmp.__target?.id === datapoint.__target?.id
    );
  }
}
