import { Injectable } from '@angular/core';
import { BsModalService } from 'ngx-bootstrap/modal';
import { KPIDetails } from './datapoint-selection.model';
import { DatapointSelectorModalComponent } from './datapoint-selector-modal/datapoint-selector-modal.component';
import { DatapointSelectorModalOptions } from './datapoint-selector-modal/datapoint-selector-modal.model';

@Injectable({ providedIn: 'root' })
export class DatapointSelectorService {
  constructor(protected modal: BsModalService) {}

  selectDataPoints(
    initialState: Partial<DatapointSelectorModalOptions> = {}
  ): Promise<KPIDetails[]> {
    const modal = this.modal.show(DatapointSelectorModalComponent, {
      ignoreBackdropClick: true,
      keyboard: false,
      initialState,
      ariaDescribedby: 'modal-body',
      ariaLabelledBy: 'modal-title',
      class: 'modal-lg'
    });
    const content = modal.content as DatapointSelectorModalComponent;
    return content.result;
  }
}
