import { Component, ContentChild, EventEmitter, forwardRef, Input, Output } from '@angular/core';
import {
  AbstractControl,
  ControlValueAccessor,
  FormBuilder,
  FormGroup,
  NG_VALIDATORS,
  NG_VALUE_ACCESSOR,
  ValidationErrors,
  Validator
} from '@angular/forms';
import { IResultList } from '@c8y/client';
import { Observable, pipe } from 'rxjs';
import { map, startWith, take } from 'rxjs/operators';
import { ForOfFilterPipe, ListItemDragHandleComponent } from '@c8y/ngx-components';
import {
  DatapointAction,
  DatapointAttributesFormConfig,
  DATAPOINT_LIBRARY_FRAGMENT,
  KPIDetails,
  ManagedObjectKPI
} from '../datapoint-selection.model';

@Component({
  selector: 'c8y-datapoint-selector-list-item',
  templateUrl: './datapoint-selector-list-item.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => DatapointSelectorListItemComponent),
      multi: true
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => DatapointSelectorListItemComponent),
      multi: true
    }
  ]
})
export class DatapointSelectorListItemComponent implements ControlValueAccessor, Validator {
  formGroup: FormGroup;
  @Input() defaultFormOptions: Partial<DatapointAttributesFormConfig> = {};
  @Input() isSelected = false;
  @Input() isCollapsed = true;
  @Input() showAddRemoveButton = true;
  @Input() editable = true;
  @Input() showActiveToggle = false;
  @Input() activeToggleDisabled = false;
  @Input() showOptions = false;
  @Input() datapointLibraryEntries: Observable<IResultList<ManagedObjectKPI>>;
  @Input() actions: DatapointAction[] = [];
  @Input() optionToRemove = false;
  @Input() hasUnlinkTemplateOption = false;
  @Output() added = new EventEmitter<KPIDetails>();
  @Output() removed = new EventEmitter<KPIDetails>();
  @Input() colorPickerDisabled = true;
  @Input() disableTypeaheadIfSelected = false;
  @Input() highlightText: string;
  @ContentChild(ListItemDragHandleComponent) dragHandle: ListItemDragHandleComponent;

  pattern = '';
  filterPipe: ForOfFilterPipe<ManagedObjectKPI>;
  isValid$: Observable<boolean>;

  constructor(private formBuilder: FormBuilder) {
    this.formGroup = this.formBuilder.group({
      details: [],
      color: [],
      __active: [],
      __target: [],
      fragment: [],
      series: [],
      __template: []
    });
    this.isValid$ = this.formGroup.statusChanges.pipe(
      map(status => status === 'VALID'),
      startWith(this.formGroup.valid)
    );
  }

  validate(_control: AbstractControl): ValidationErrors {
    return this.formGroup?.valid ? null : { formInvalid: {} };
  }

  writeValue(obj: any): void {
    this.formGroup.patchValue({ ...obj, details: obj });
  }

  registerOnChange(fn: any): void {
    this.formGroup.valueChanges.pipe(map(tmp => this.transformFormValue(tmp))).subscribe(fn);
  }

  registerOnTouched(fn: any): void {
    this.formGroup.valueChanges.pipe(take(1)).subscribe(fn);
  }

  setDisabledState(isDisabled: boolean): void {
    if (this.formGroup?.disabled === isDisabled) {
      return;
    }
    isDisabled ? this.formGroup.disable() : this.formGroup.enable();
  }

  collapse() {
    this.isCollapsed = !this.isCollapsed;
  }

  addOrRemoveItem() {
    const value = this.transformFormValue(this.formGroup.value);
    if (this.isSelected) {
      this.removed.emit(value);
    } else {
      this.added.emit(value);
    }
  }

  remove() {
    this.removed.emit(this.transformFormValue(this.formGroup.value));
  }

  setPipe(filterStr: string) {
    this.pattern = filterStr;
    this.filterPipe = pipe(
      map((data: ManagedObjectKPI[]) => {
        return this.filterDatapointLabel(data, filterStr);
      })
    );
  }

  unlinkDatapointTemplate(): void {
    const details = this.formGroup.value.details || {};
    this.resetUnusedProperties(details);
    this.formGroup.patchValue({ __template: undefined, details });
  }

  dataPointTemplateSelected(template: ManagedObjectKPI) {
    const attributesToAssign: Array<keyof KPIDetails> = [
      'color',
      'label',
      'min',
      'max',
      'yellowRangeMax',
      'yellowRangeMin',
      'redRangeMax',
      'redRangeMin',
      'target',
      'orientation',
      'unit'
    ];
    const { fragment, series, __target, __active } = this.formGroup.value;
    const dataPoint: KPIDetails = {
      fragment,
      series,
      __active,
      __target,
      __template: template.id
    };
    for (const attribute of attributesToAssign) {
      const value = template[DATAPOINT_LIBRARY_FRAGMENT][attribute];
      dataPoint[attribute] = value;
    }
    this.writeValue(dataPoint);
    this.setPipe('');
  }

  private resetUnusedProperties(details: Partial<KPIDetails>): void {
    const { showTarget, showYellowRange, showRedRange } = this.defaultFormOptions;
    details.__template = undefined;
    if (!showTarget) {
      details.target = undefined;
    }
    if (!showYellowRange) {
      details.yellowRangeMin = undefined;
      details.yellowRangeMax = undefined;
    }
    if (!showRedRange) {
      details.redRangeMin = undefined;
      details.redRangeMax = undefined;
    }
  }

  private filterDatapointLabel(kpis: ManagedObjectKPI[], filterStr: string): ManagedObjectKPI[] {
    return kpis.filter(
      (mo: ManagedObjectKPI) =>
        mo[DATAPOINT_LIBRARY_FRAGMENT] &&
        mo[DATAPOINT_LIBRARY_FRAGMENT].label &&
        typeof mo[DATAPOINT_LIBRARY_FRAGMENT].label === 'string' &&
        mo[DATAPOINT_LIBRARY_FRAGMENT].label.toLowerCase().indexOf(filterStr.toLowerCase()) > -1
    );
  }

  private transformFormValue(formValue: any) {
    const obj = Object.assign({}, formValue.details || {}, formValue);
    delete obj.details;
    return obj;
  }
}
