import { Component, ElementRef, OnInit } from '@angular/core';
import { IIdentified } from '@c8y/client';
import { gettext } from '@c8y/ngx-components';
import { get } from 'lodash-es';
import { BsModalRef } from 'ngx-bootstrap/modal';
import {
  DatapointSelectorModalAssetSelectorOptions,
  KPIDetails
} from '../datapoint-selection.model';

@Component({
  selector: 'c8y-datapoint-selector-modal',
  templateUrl: './datapoint-selector-modal.component.html'
})
export class DatapointSelectorModalComponent implements OnInit {
  contextAsset: IIdentified;
  allowChangingContext = true;
  allowSearch = true;
  allowDatapointsFromMultipleAssets = true;
  selectedDatapoints = new Array<KPIDetails>();
  title: string = gettext('Data point selector');
  saveButtonLabel: string = gettext('Add data points');
  guessDatapointUnit = true;
  defaultActiveState = true;
  finishWithFirstSelection = false;
  ignoreDatapointTemplates = false;
  assetSelectorConfig: Partial<DatapointSelectorModalAssetSelectorOptions> = {};
  readonly result: Promise<KPIDetails[]> = new Promise((resolve, reject) => {
    this.save = resolve;
    this.cancel = reject;
  });

  private save: (value: KPIDetails[]) => void;
  private cancel: (reason?: any) => void;

  constructor(private bsModal: BsModalRef, private elementRef: ElementRef) {}

  ngOnInit(): void {
    this.applyWorkaroundForAngularJSModalsThatMightBeOpenedInParallel();
  }

  saveChanges(): void {
    this.bsModal.hide();
    this.save(this.selectedDatapoints);
  }

  close() {
    this.bsModal.hide();
    this.cancel();
  }

  selectionChange(selection: Array<KPIDetails>) {
    this.selectedDatapoints = selection;
    if (this.finishWithFirstSelection) {
      this.saveChanges();
    }
  }

  /**
   * changes the z-index of the modal, to make sure this modal is on top.
   * required as angularJS modals are placing them selves on top via adjusting z-index.
   * while angular modals use the order within the dom for this..
   */
  private applyWorkaroundForAngularJSModalsThatMightBeOpenedInParallel() {
    const modalContainer: HTMLElement = get(
      this.elementRef,
      'nativeElement.parentElement.parentElement.parentElement'
    );
    modalContainer.style.zIndex = '1060';
  }
}
