import { Component, forwardRef, Input, OnInit } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { IIdentified, IResultList } from '@c8y/client';
import { gettext } from '@c8y/ngx-components';
import { BehaviorSubject, combineLatest, from, Observable } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  map,
  shareReplay,
  switchMap,
  tap
} from 'rxjs/operators';
import { DatapointLibraryService } from './datapoint-library.service';
import { KPIDetails, ManagedObjectKPI } from './datapoint-selection.model';

@Component({
  selector: 'c8y-datapoint-selector',
  templateUrl: './datapoint-selector.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: forwardRef(() => DatapointSelectorComponent)
    }
  ]
})
export class DatapointSelectorComponent implements OnInit, ControlValueAccessor {
  @Input() contextAsset: IIdentified;
  @Input() allowChangingContext = true;
  @Input() allowDatapointsFromMultipleAssets = true;
  selectedDatapoints = new Array<KPIDetails>();
  @Input() defaultActiveState = true;
  @Input() ignoreDatapointTemplates = false;
  @Input() guessDatapointUnit = true;
  @Input() allowSearch = true;
  @Input() hideSelection = false;
  searchString = '';
  maxNumberOfDatapoints = 50;

  loadingDatapoints = false;
  assetSelection = new BehaviorSubject<IIdentified>(null);
  datapoints$: Observable<KPIDetails[]>;
  filteredDatapoints$: Observable<KPIDetails[]>;
  searchStringChanges$: Observable<string>;
  datapointLibraryEntries: Observable<IResultList<ManagedObjectKPI>>;

  emptyStateSubtitleWhenNoMatchingDataPoints = gettext('Try another search term.');
  emptyStateSubtitleWhenNoDataPointsInAsset = gettext(
    'Select an asset with data points from the list.'
  );

  private searchString$ = new BehaviorSubject('');
  private touched = false;
  private onChange: (quantity: KPIDetails[]) => void;
  private onTouched: () => void;

  constructor(private datapointService: DatapointLibraryService) {}

  ngOnInit(): void {
    this.setupObservables();
    if (!this.ignoreDatapointTemplates) {
      this.datapointLibraryEntries = from(this.datapointService.getFirstDatapointLibraryPage());
    }

    if (this.contextAsset) {
      this.selectionChanged(this.contextAsset);
    }
  }

  writeValue(obj: KPIDetails[]): void {
    this.selectedDatapoints = obj;
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  datapointAdded(dp: KPIDetails): void {
    this.markAsTouched();
    dp.__active = this.defaultActiveState;
    if (this.guessDatapointUnit && !dp.unit) {
      this.datapointService.guessUnitOfDatapoint(dp.fragment, dp.series, dp.__target).then(unit => {
        dp.unit = unit;
      });
    }
    this.selectedDatapoints = [...this.selectedDatapoints, dp];
    this.emitCurrentSelection();
  }

  datapointRemoved(dp: KPIDetails): void {
    this.markAsTouched();
    this.selectedDatapoints = this.selectedDatapoints.filter(
      tmp =>
        tmp.fragment !== dp.fragment ||
        tmp.series !== dp.series ||
        tmp.__target?.id !== dp.__target?.id
    );
    this.emitCurrentSelection();
  }

  selectionChanged(evt: IIdentified | IIdentified[]): void {
    if (Array.isArray(evt) && evt.length !== 0) {
      return this.selectAsset(evt[0]);
    }

    if (!Array.isArray(evt) && evt.items) {
      return this.selectionChanged(evt.items);
    }

    if (!Array.isArray(evt) && evt.id) {
      return this.selectAsset(evt);
    }

    // reset selection
    this.assetSelection.next(null);
  }

  trackByFn(_index: number, item: KPIDetails): string {
    return `${item.fragment}-${item.__target?.id}-${item.series}`;
  }

  searchStringChanged(newValue = ''): void {
    this.searchString$.next(newValue);
    this.searchString = newValue;
  }

  private setupObservables(): void {
    this.datapoints$ = this.assetSelection.pipe(
      tap(() => {
        this.loadingDatapoints = true;
      }),
      switchMap(asset =>
        asset?.id
          ? this.datapointService.getDatapointsOfAsset(asset, this.ignoreDatapointTemplates)
          : []
      ),
      tap(() => (this.loadingDatapoints = false)),
      shareReplay(1)
    );

    this.searchStringChanges$ = this.searchString$.pipe(
      distinctUntilChanged(),
      debounceTime(500),
      shareReplay(1)
    );

    this.filteredDatapoints$ = combineLatest([this.searchStringChanges$, this.datapoints$]).pipe(
      map(([searchString, datapoints]) => {
        if (!searchString) {
          return datapoints;
        }
        const lowerCaseSearchString = searchString.toLowerCase();
        return datapoints.filter(datapoint =>
          this.includesSearchString(datapoint, lowerCaseSearchString)
        );
      }),
      map(filtered => filtered.slice(0, this.maxNumberOfDatapoints))
    );
  }

  private selectAsset(asset: IIdentified) {
    this.assetSelection.next(asset);
    this.searchStringChanged();
    if (!this.allowDatapointsFromMultipleAssets) {
      this.clearSelection();
    }
  }

  private clearSelection(): void {
    this.selectedDatapoints = [];
    this.emitCurrentSelection();
  }

  private emitCurrentSelection() {
    this.onChange(this.selectedDatapoints);
  }

  private markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  private includesSearchString(datapoint: KPIDetails, lowerCaseSearchString: string): boolean {
    const label = datapoint.label?.toLowerCase();
    if (label && label.includes(lowerCaseSearchString)) {
      return true;
    }

    const fragment = datapoint.fragment?.toLowerCase();
    if (fragment && fragment.includes(lowerCaseSearchString)) {
      return true;
    }

    const series = datapoint.series?.toLowerCase();
    if (series && series.includes(lowerCaseSearchString)) {
      return true;
    }

    return false;
  }
}
