import { Component, forwardRef, Input } from '@angular/core';
import {
  AbstractControl,
  ControlValueAccessor,
  FormGroup,
  NG_VALIDATORS,
  NG_VALUE_ACCESSOR,
  ValidationErrors,
  Validator
} from '@angular/forms';
import { map, take } from 'rxjs/operators';
import { DatapointAttributesFormValidationService } from './datapoint-attributes-form-validation.service';
import { AXIS_TYPES, CHART_LINE_TYPES, CHART_RENDER_TYPES } from './datapoint-attributes.model';

@Component({
  selector: 'c8y-datapoint-attributes-form',
  templateUrl: './datapoint-attributes-form.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => DatapointAttributesFormComponent),
      multi: true
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => DatapointAttributesFormComponent),
      multi: true
    }
  ]
})
export class DatapointAttributesFormComponent implements ControlValueAccessor, Validator {
  @Input() showTarget = true;
  @Input() showRange = true;
  @Input() showYellowRange = true;
  @Input() showRedRange = true;
  @Input() showChart = true;
  @Input() showFormIfTemplateWasSelected = false;
  formGroup: FormGroup;
  range: FormGroup;
  yellowRange: FormGroup;
  redRange: FormGroup;
  chart: FormGroup;
  rawValue: any = {};
  CHART_RENDER_TYPES = CHART_RENDER_TYPES;
  CHART_LINE_TYPES = CHART_LINE_TYPES;
  AXIS_TYPES = AXIS_TYPES;

  customValidationErrorMessages: {
    [formField: string]: {
      [validationError: string]: string;
    };
  } = {};

  constructor(private formValidations: DatapointAttributesFormValidationService) {
    this.formGroup = this.formValidations.getDefaultFormGroup();
    this.setSubForms();
  }

  validate(_control: AbstractControl): ValidationErrors {
    return this.formGroup?.valid ? null : { formInvalid: {} };
  }

  writeValue(obj: any): void {
    this.rawValue = obj || {};
    if (obj) {
      this.formGroup.patchValue(this.formValidations.convertToFormGroupFormat(obj), {
        emitEvent: false
      });
    }
  }

  registerOnChange(fn: any): void {
    this.formGroup.valueChanges
      .pipe(
        map(this.formValidations.convertToBackendFormat),
        map(formValue => Object.assign(this.rawValue, formValue))
      )
      .subscribe(fn);
  }

  registerOnTouched(fn: any): void {
    this.formGroup.valueChanges.pipe(take(1)).subscribe(fn);
  }

  setDisabledState(isDisabled: boolean): void {
    if (this.formGroup?.disabled === isDisabled) {
      return;
    }
    isDisabled ? this.formGroup.disable() : this.formGroup.enable();
  }

  private setSubForms() {
    if (!this.formGroup) {
      this.range = this.yellowRange = this.redRange = this.chart = undefined;
      return;
    }
    this.range = this.formGroup.get('range') as FormGroup;
    this.yellowRange = this.formGroup.get('yellowRange') as FormGroup;
    this.redRange = this.formGroup.get('redRange') as FormGroup;
    this.chart = this.formGroup.get('chart') as FormGroup;
  }
}
