import { Pipe, PipeTransform } from '@angular/core';
import { KPIDetails } from '../datapoint-selection.model';

@Pipe({
  name: 'datapointLabel'
})
export class DatapointLabelPipe implements PipeTransform {
  transform(
    datapoint: KPIDetails,
    options = { includeDevice: false, doNotUseLabel: false }
  ): string {
    let label = '';
    try {
      if (!options?.doNotUseLabel && datapoint.label) {
        label = datapoint.label;
      } else {
        label = `${datapoint.fragment} → ${datapoint.series}`;
      }

      if (options?.includeDevice && (datapoint.__target?.name || datapoint.__target?.id)) {
        label += ` (${datapoint.__target?.name || datapoint.__target?.id})`;
      }
    } catch (e) {
      // nothing to do
    }

    return label;
  }
}
